async function parseCardsInMessage(message: string) {

  // Ð˜Ñ‰ÐµÐ¼ Ð¼Ð°Ñ€ÐºÐµÑ€Ñ‹ Ñ ÑƒÑ‡ÐµÑ‚Ð¾Ð¼ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ñ‹Ñ… Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð² Ð¸ Ð¿ÐµÑ€ÐµÐ½Ð¾ÑÐ¾Ð² ÑÑ‚Ñ€Ð¾Ðº
  const cardRegex = /\[CARD:(SPECIALIST|SERVICE):(\d+)\]/gi;
  const cards: any[] = [];
  let match;

  while ((match = cardRegex.exec(message)) !== null) {
    const [fullMatch, type, id] = match;
    const cardId = parseInt(id);

    try {
      if (type === "SPECIALIST") {
        const specialist = await prisma.specialist.findUnique({
          where: { id: cardId },
          select: {
            id: true,
            name: true,
            qualification: true,
            experience: true,
            specialization: true,
            image_url: true,
            education: true,
            category: {
              select: {
                slug: true,
              },
            },
          },
        });

        if (specialist) {
          cards.push({
            type: "specialist",
            data: {
              ...specialist,
              categorySlug: specialist.category?.slug || "specialists",
            },
            placeholder: fullMatch,
          });
        } else {
        }
      } else if (type === "SERVICE") {
        const service = await prisma.service.findUnique({
          where: { id: cardId },
          select: {
            id: true,
            title: true,
            description: true,
            category: {
              select: {
                name: true,
                slug: true,
              },
            },
            price: true,
          },
        });

        if (service) {
          // ÐÐ¾Ñ€Ð¼Ð°Ð»Ð¸Ð·ÑƒÐµÐ¼ Ð´Ð°Ð½Ð½Ñ‹Ðµ: category Ð¸Ð· Ð¾Ð±ÑŠÐµÐºÑ‚Ð° Ð² ÑÑ‚Ñ€Ð¾ÐºÑƒ + Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ slug
          const normalizedService = {
            ...service,
            category: service.category?.name || "ÐÐµ ÑƒÐºÐ°Ð·Ð°Ð½Ð¾",
            categorySlug: service.category?.slug || "services",
          };
          cards.push({
            type: "service",
            data: normalizedService,
            placeholder: fullMatch,
          });
        } else {
        }
      }
    } catch (error) {
    }
  }

  return { message, cards };
}

async function getClinicContext() {
  try {
    const contacts = await prisma.contacts.findFirst();

    const services = await prisma.service.findMany({
      select: {
        id: true,
        title: true,
        description: true,
        category: {
          select: {
            name: true,
          },
        },
      },
    });

    const specialists = await prisma.specialist.findMany({
      select: {
        id: true,
        name: true,
        qualification: true,
        experience: true,
        specialization: true,
      },
    });

    // ÐÐ¾Ñ€Ð¼Ð°Ð»Ð¸Ð·ÑƒÐµÐ¼ Ð´Ð°Ð½Ð½Ñ‹Ðµ: Ð¿Ñ€ÐµÐ¾Ð±Ñ€Ð°Ð·ÑƒÐµÐ¼ Ð²Ð»Ð¾Ð¶ÐµÐ½Ð½Ñ‹Ðµ Ð¾Ð±ÑŠÐµÐºÑ‚Ñ‹ Ð² ÑÑ‚Ñ€Ð¾ÐºÐ¸
    const normalizedServices = services.map((s) => ({
      ...s,
      category: s.category?.name || "ÐÐµ ÑƒÐºÐ°Ð·Ð°Ð½Ð¾",
    }));

    return {
      contacts,
      services: normalizedServices,
      specialists,
    };
  } catch (error) {
    if (error instanceof Error) {
    }
    return null;
  }
}

